import { getFormStatus } from "@/helpers/FormsHelpers";

export const generalModule = {
  namespaced: true,
  state: {
    email: "",
    technology: "",
    information: {
      russian: "",
      english: "",
    },
  },
  getters: {
    getRussianInformation(state) {
      return state.information.russian;
    },
    getEnglishInformation(state) {
      return state.information.english;
    },
    getEmail(state) {
      return state.email;
    },
    getTechnology(state) {
      return state.technology;
    },
    getFormStatus(state) {
      const hasEmail = !!state.email;
      const hasTechnology = !!state.technology;
      const hasRussian = !!state.information.russian;
      const hasEnglish = !!state.information.english;

      const isFilled = hasRussian && hasEnglish && hasEmail && hasTechnology;
      const isNotFilled =
        !hasRussian && !hasEnglish && !hasEmail && !hasTechnology;

      return getFormStatus({ isFilled, isNotFilled });
    },
  },
  mutations: {
    changeRussianInformation(state, value) {
      state.information.russian = value;
    },
    changeEnglishInformation(state, value) {
      state.information.english = value;
    },
    changeEmail(state, value) {
      state.email = value;
    },
    changeTechnology(state, value) {
      state.technology = value;
    },
    setForm(state, data) {
      state.email = data.email;
      state.technology = data.technology;
      state.information = data.information;
    },
  },
};
